form {
    display: flex;
    flex-direction: column;
    width: 400px;
}
h1 {
    color: darkblue;
}
span {
    color: red;
}
.inputText {
    margin-top: 10px;
}
.textArea {
    margin-top: 10px;
    margin-bottom: 10px;
}
.button {
    background-color: #1976d2 !important;
    color: #fff !important;
}
.has-error {
    border: solid 1px red !important;
    margin-top: 10px;

}
.listItem {
    display: flex;
    background-color: #e0edfa;
    color:#5c5d5e;
    padding-top: 10px;
    padding-bottom: 10px;
    align-items: center;
    justify-content: center;
    border-radius: 10px !important;
}

.par {
    display: flex;
    background-color: #e0edfa;
    color:#5c5d5e;
    padding-top: 10px;
    padding-bottom: 10px;
    align-items: center;
    justify-content: center;
    border-radius: 10px !important;
    cursor: pointer;
}
.impar {
    display: flex;
    background-color: #fff;
    color:#5c5d5e;
    padding-top: 10px;
    padding-bottom: 10px;
    align-items: center;
    justify-content: center;
    border-radius: 10px !important;
    cursor: pointer;
}